import {
  DataSourceInstanceSettings,
  CoreApp,
  ScopedVars,
  AdHocVariableFilter,
} from '@grafana/data';
import { DataSourceWithBackend, getTemplateSrv, TemplateSrv } from '@grafana/runtime';
import { DynamoQuery, FluvioDataSourceOptions, DEFAULT_QUERY } from './types';

// Development mode detection
const isDevelopmentMode = () => {
  // Check if we're in development based on various indicators
  return process.env.NODE_ENV === 'development' || 
         window.location.hostname === 'localhost' ||
         window.location.hostname === '127.0.0.1' ||
         window.location.port === '3000';
};

const devLog = (message: string, ...args: any[]) => {
  if (isDevelopmentMode()) {
    console.log(`üîß [FLUVIO-DEV] ${message}`, ...args);
  }
};

const devWarn = (message: string, ...args: any[]) => {
  if (isDevelopmentMode()) {
    console.warn(`‚ö†Ô∏è [FLUVIO-DEV] ${message}`, ...args);
  }
};

const devError = (message: string, ...args: any[]) => {
  if (isDevelopmentMode()) {
    console.error(`‚ùå [FLUVIO-DEV] ${message}`, ...args);
  }
};

export class DataSource extends DataSourceWithBackend<DynamoQuery, FluvioDataSourceOptions> {
  instanceSettings: DataSourceInstanceSettings<FluvioDataSourceOptions>;
  templateSrv: TemplateSrv;

  constructor(instanceSettings: DataSourceInstanceSettings<FluvioDataSourceOptions>) {
    super(instanceSettings);
    this.instanceSettings = instanceSettings;
    this.templateSrv = getTemplateSrv();
    
    // Development mode logging
    if (isDevelopmentMode()) {
      console.log('üöÄ FLUVIO DYNAMODB PLUGIN LOADED IN DEVELOPMENT MODE!');
      console.log('üìÖ Load time:', new Date().toISOString());
      console.log('üîß Development features enabled: Enhanced logging, cache disabled');
      devLog('Instance Settings:', instanceSettings);
      devLog('Plugin URL:', instanceSettings.url);
      devLog('Plugin ID:', instanceSettings.id);
      
      // Validate instance settings in dev mode
      if (!instanceSettings.url) {
        devWarn('Plugin URL not configured in instance settings');
      }
      if (!instanceSettings.jsonData?.region) {
        devWarn('AWS region not configured in plugin settings');
      }
    } else {
      console.log('üöÄ Fluvio DynamoDB Plugin loaded (Production mode)');
    }
  }

  // Default query shown when adding a new panel
  getDefaultQuery(app: CoreApp): Partial<DynamoQuery> {
    return DEFAULT_QUERY;
  }

  // Return false to prevent queries with no input
  filterQuery(query: DynamoQuery): boolean {
    return !!query.partiql || !!query.table;
  }

  // Apply template variables to queries before sending to backend
  applyTemplateVariables(query: DynamoQuery, scopedVars: ScopedVars, filters?: AdHocVariableFilter[]): DynamoQuery {
    devLog('applyTemplateVariables called', { timestamp: Date.now() });
    devLog('Scoped Variables:', scopedVars);
    devLog('Query before processing:', query);
    devLog('Ad-hoc filters:', filters);

    // Create enhanced variables object with all Grafana built-in variables
    const variables = this.buildVariablesContext(scopedVars, query, filters);
    
    devLog('Enhanced variables context:', variables);

    // Apply template variable interpolation to all query fields
    const interpolatedQuery = this.interpolateQueryFields(query, variables);
    
    devLog('Query after template variable processing:', interpolatedQuery);

    return interpolatedQuery;
  }

  /**
   * Build comprehensive variables context including Grafana built-ins and custom variables
   */
  private buildVariablesContext(scopedVars: ScopedVars, query: DynamoQuery, filters?: AdHocVariableFilter[]) {
    const { fromUnix, toUnix, fromIso, toIso } = this.resolveTimeRange(scopedVars, query);
    
    // Start with scoped variables (includes custom dashboard variables)
    const variables = { ...scopedVars };

    // Add/override Grafana built-in time variables
    variables.__from = { value: fromUnix.toString(), text: fromUnix.toString() };
    variables.__to = { value: toUnix.toString(), text: toUnix.toString() };
    variables.__fromIso = { value: fromIso, text: fromIso };
    variables.__toIso = { value: toIso, text: toIso };
    
    // Add interval variables (these come from Grafana's time range)
    const timeRange = toUnix - fromUnix;
    const intervalSeconds = Math.max(1, Math.floor(timeRange / 1000)); // At least 1 second
    variables.__interval = { value: `${intervalSeconds}s`, text: `${intervalSeconds}s` };
    variables.__interval_ms = { value: (intervalSeconds * 1000).toString(), text: (intervalSeconds * 1000).toString() };

    // Build enhanced __timeFilter for PartiQL queries
    const timestampField = this.interpolateString(query.timestampField || 'timestamp', variables);
    const quotedTimeField = this.formatPartiqlIdentifier(timestampField);
    
    if (query.timeFilterEnabled) {
      // Create flexible time filter that works with different timestamp formats
      const timeFilterConditions = [
        `${quotedTimeField} BETWEEN ${fromUnix} AND ${toUnix}`, // Unix timestamp
        `${quotedTimeField} BETWEEN '${fromIso}' AND '${toIso}'`, // ISO string
        `CAST(${quotedTimeField} AS NUMBER) BETWEEN ${fromUnix} AND ${toUnix}` // Cast to number if needed
      ];
      
      const timeFilterValue = timeFilterConditions[0]; // Use Unix timestamp as primary
      variables.__timeFilter = { value: timeFilterValue, text: timeFilterValue };
      
      // Additional time filter variants for different use cases
      variables.__timeFilterIso = { 
        value: `${quotedTimeField} BETWEEN '${fromIso}' AND '${toIso}'`, 
        text: `${quotedTimeField} BETWEEN '${fromIso}' AND '${toIso}'` 
      };
      variables.__timeFilterMs = { 
        value: `${quotedTimeField} BETWEEN ${fromUnix * 1000} AND ${toUnix * 1000}`, 
        text: `${quotedTimeField} BETWEEN ${fromUnix * 1000} AND ${toUnix * 1000}` 
      };
      
      devLog('Time filter created:', {
        field: quotedTimeField,
        from: fromIso,
        to: toIso,
        filter: timeFilterValue
      });
    } else {
      variables.__timeFilter = { value: '1=1', text: '1=1' }; // Always true condition
      variables.__timeFilterIso = { value: '1=1', text: '1=1' };
      variables.__timeFilterMs = { value: '1=1', text: '1=1' };
      devLog('Time filter disabled, using 1=1');
    }

    // Add ad-hoc filter variables if present
    if (filters && filters.length > 0) {
      const adhocConditions = filters.map(filter => {
        const quotedKey = this.formatPartiqlIdentifier(filter.key);
        const operator = this.mapFilterOperator(filter.operator);
        const value = this.formatFilterValue(filter.value, filter.operator);
        return `${quotedKey} ${operator} ${value}`;
      }).join(' AND ');
      
      variables.__adhocFilters = { value: adhocConditions, text: adhocConditions };
      devLog('Ad-hoc filters created:', adhocConditions);
    } else {
      variables.__adhocFilters = { value: '1=1', text: '1=1' };
    }

    return variables;
  }

  /**
   * Interpolate template variables in all query fields
   */
  private interpolateQueryFields(query: DynamoQuery, variables: ScopedVars): DynamoQuery {
    return {
      ...query,
      // Time fields (already processed in buildVariablesContext)
      timeFrom: variables.__fromIso?.value || query.timeFrom,
      timeTo: variables.__toIso?.value || query.timeTo,
      
      // PartiQL query with comprehensive variable support
      partiql: query.partiql ? this.interpolatePartiQLQuery(query.partiql, variables) : query.partiql,
      
      // Table and key fields with variable support
      table: this.interpolateString(query.table, variables),
      partitionKeyName: this.interpolateString(query.partitionKeyName, variables),
      partitionKeyValue: this.interpolateString(query.partitionKeyValue, variables),
      sortKeyName: this.interpolateString(query.sortKeyName, variables),
      sortKeyValue: this.interpolateString(query.sortKeyValue, variables),
      timestampField: this.interpolateString(query.timestampField, variables),
      
      // Field mappings with variable support
      fieldMappings: query.fieldMappings?.map((mapping) => ({
        ...mapping,
        sourcePath: this.interpolateString(mapping.sourcePath, variables) || mapping.sourcePath,
        fieldName: this.interpolateString(mapping.fieldName, variables) || mapping.fieldName,
      })) || query.fieldMappings,
    };
  }

  /**
   * Enhanced PartiQL query interpolation with special handling for multi-value variables
   */
  private interpolatePartiQLQuery(partiql: string, variables: ScopedVars): string {
    const originalQuery = partiql;
    
    // First pass: Handle multi-value variables with custom formatting
    let processedQuery = this.templateSrv.replace(partiql, variables, (value: any, variable: any) => {
      if (Array.isArray(value)) {
        // Handle multi-value variables for PartiQL
        if (variable && typeof variable === 'object' && variable.multi) {
          // For PartiQL IN clauses, format as ('value1', 'value2', 'value3')
          const formattedValues = value.map(v => {
            // Check if value is already quoted or is a number
            if (typeof v === 'number' || /^\d+$/.test(v)) {
              return v.toString();
            }
            // Add quotes for string values, escape existing quotes
            return `'${v.toString().replace(/'/g, "''")}'`;
          }).join(', ');
          return `(${formattedValues})`;
        }
      }
      
      // Single value or non-multi variable - use default formatting
      if (typeof value === 'string' && !value.includes("'") && !/^\d+$/.test(value)) {
        // Auto-quote string values that aren't already quoted and aren't numbers
        return `'${value}'`;
      }
      
      return value;
    });

    devLog('PartiQL interpolation:', {
      original: originalQuery,
      processed: processedQuery,
      variables: Object.keys(variables)
    });

    return processedQuery;
  }

  /**
   * Safe string interpolation with null/undefined handling
   */
  private interpolateString(value: string | undefined, variables: ScopedVars): string | undefined {
    if (!value) {
      return value;
    }
    
    try {
      return this.templateSrv.replace(value, variables);
    } catch (error) {
      devError('Error interpolating string:', error, { value, variables: Object.keys(variables) });
      return value; // Return original value if interpolation fails
    }
  }



  private resolveTimeRange(scopedVars: ScopedVars, query: DynamoQuery) {
    const now = Date.now();
    const defaultFrom = now - 24 * 60 * 60 * 1000;
    const defaultTo = now;

    let fromMs: number | undefined;
    let toMs: number | undefined;

    // Only use time from Grafana time picker when available
    if (scopedVars.__from?.value !== undefined) {
      fromMs = this.coerceToMillis(scopedVars.__from.value);
    }
    if (scopedVars.__to?.value !== undefined) {
      toMs = this.coerceToMillis(scopedVars.__to.value);
    }

    // Check query object if scopedVars don't have time
    if (!fromMs && query.timeFrom) {
      fromMs = this.coerceToMillis(query.timeFrom);
    }
    if (!toMs && query.timeTo) {
      toMs = this.coerceToMillis(query.timeTo);
    }

    // Fallback to defaults
    fromMs = fromMs ?? defaultFrom;
    toMs = toMs ?? defaultTo;

    const from = Math.min(fromMs, toMs);
    const to = Math.max(fromMs, toMs);

    return {
      fromUnix: Math.floor(from / 1000),
      toUnix: Math.floor(to / 1000),
      fromIso: new Date(from).toISOString(),
      toIso: new Date(to).toISOString(),
    };
  }

  private coerceToMillis(candidate: any): number | undefined {
    if (candidate === undefined || candidate === null) {
      return undefined;
    }

    if (typeof candidate === 'number') {
      if (!Number.isFinite(candidate)) {
        return undefined;
      }
      return this.normalizeEpoch(candidate);
    }

    if (typeof candidate === 'string') {
      const trimmed = candidate.trim();
      if (trimmed === '') {
        return undefined;
      }

      const numeric = Number(trimmed);
      if (!Number.isNaN(numeric)) {
        return this.normalizeEpoch(numeric);
      }

      const parsed = Date.parse(trimmed);
      if (!Number.isNaN(parsed)) {
        return parsed;
      }

      return undefined;
    }

    if (candidate instanceof Date) {
      const time = candidate.getTime();
      return Number.isFinite(time) ? time : undefined;
    }

    if (typeof (candidate as any).toMillis === 'function') {
      const millis = (candidate as any).toMillis();
      if (Number.isFinite(millis)) {
        return millis;
      }
    }

    if (typeof candidate.valueOf === 'function') {
      const valueOfResult = candidate.valueOf();
      if (typeof valueOfResult === 'number' && Number.isFinite(valueOfResult)) {
        return this.normalizeEpoch(valueOfResult);
      }
    }

    if (typeof (candidate as any).toISOString === 'function') {
      const iso = (candidate as any).toISOString();
      const parsed = Date.parse(iso);
      if (!Number.isNaN(parsed)) {
        return parsed;
      }
    }

    return undefined;
  }

  private normalizeEpoch(value: number): number {
    // Treat values smaller than 1e11 (~3,170 years in ms) as seconds
    return value < 1e11 ? value * 1000 : value;
  }

  private formatPartiqlIdentifier(identifier?: string): string {
    if (!identifier) {
      return '"timestamp"';
    }

    const trimmed = identifier.trim();

    if (trimmed === '') {
      return '"timestamp"';
    }

    // If identifier already contains quotes, parentheses, brackets, or spaces, assume user supplied a full expression
    if (/["'\s()\[\]]/.test(trimmed)) {
      return trimmed;
    }

    // Quote each dot-delimited segment to protect reserved words
    return trimmed
      .split('.')
      .map((segment) => `"${segment.replace(/"/g, '""')}"`)
      .join('.');
  }

  /**
   * Map Grafana filter operators to PartiQL operators
   */
  private mapFilterOperator(operator: string): string {
    const operatorMap: Record<string, string> = {
      '=': '=',
      '!=': '<>',
      '>': '>',
      '<': '<',
      '>=': '>=',
      '<=': '<=',
      '=~': 'LIKE', // Regex-like matching
      '!~': 'NOT LIKE',
      'in': 'IN',
      'not in': 'NOT IN',
    };
    
    return operatorMap[operator] || '=';
  }

  /**
   * Format filter values for PartiQL queries
   */
  private formatFilterValue(value: string, operator: string): string {
    // Handle IN/NOT IN operators with arrays
    if (operator === 'in' || operator === 'not in') {
      // If value is already formatted as array, return as-is
      if (value.startsWith('(') && value.endsWith(')')) {
        return value;
      }
      // Split comma-separated values and format as array
      const values = value.split(',').map(v => `'${v.trim().replace(/'/g, "''")}'`);
      return `(${values.join(', ')})`;
    }
    
    // Handle LIKE operators with wildcards
    if (operator === '=~' || operator === '!~') {
      // Add wildcards if not present
      const likeValue = value.includes('%') ? value : `%${value}%`;
      return `'${likeValue.replace(/'/g, "''")}'`;
    }
    
    // Handle numeric values
    if (/^\d+(\.\d+)?$/.test(value)) {
      return value;
    }
    
    // Default: quoted string value
    return `'${value.replace(/'/g, "''")}'`;
  }

  /**
   * Get available template variables for UI hints
   * Enhanced to include all Grafana built-in variables
   */
  getVariables(): string[] {
    const customVariables = this.templateSrv.getVariables().map((v) => `$${v.name}`);
    
    // Add Grafana built-in variables
    const builtInVariables = [
      '$__timeFilter',      // Main time filter
      '$__timeFilterIso',   // ISO format time filter
      '$__timeFilterMs',    // Millisecond time filter
      '$__from',            // Unix timestamp from
      '$__to',              // Unix timestamp to
      '$__fromIso',         // ISO format from
      '$__toIso',           // ISO format to
      '$__interval',        // Time interval (e.g., "5m")
      '$__interval_ms',     // Time interval in milliseconds
      '$__adhocFilters',    // Ad-hoc filter conditions
      '$__dashboard',       // Dashboard info
      '$__user',            // Current user info
      '$__org'              // Organization info
    ];
    
    return [...builtInVariables, ...customVariables];
  }

  /**
   * Get variable suggestions with descriptions for UI
   */
  getVariablesSuggestions(): Array<{ label: string; detail: string; insertText: string }> {
    const suggestions = [
      {
        label: '$__timeFilter',
        detail: 'Automatic time range filter for timestamp field',
        insertText: '$__timeFilter'
      },
      {
        label: '$__timeFilterIso',
        detail: 'Time range filter using ISO date strings',
        insertText: '$__timeFilterIso'
      },
      {
        label: '$__from',
        detail: 'Start time as Unix timestamp',
        insertText: '$__from'
      },
      {
        label: '$__to',
        detail: 'End time as Unix timestamp',
        insertText: '$__to'
      },
      {
        label: '$__fromIso',
        detail: 'Start time as ISO string',
        insertText: '$__fromIso'
      },
      {
        label: '$__toIso',
        detail: 'End time as ISO string',
        insertText: '$__toIso'
      },
      {
        label: '$__interval',
        detail: 'Auto-calculated time interval (e.g., "5m")',
        insertText: '$__interval'
      },
      {
        label: '$__adhocFilters',
        detail: 'Additional filter conditions from dashboard',
        insertText: '$__adhocFilters'
      }
    ];

    // Add custom dashboard variables
    const customVariables = this.templateSrv.getVariables().map((v) => ({
      label: `$${v.name}`,
      detail: `Custom variable: ${v.type} (${(v as any).multi ? 'multi-value' : 'single-value'})`,
      insertText: `$${v.name}`
    }));

    return [...suggestions, ...customVariables];
  }

  /**
   * Validate template variables in a query string
   */
  validateTemplateVariables(queryString: string): Array<{ variable: string; error: string }> {
    const errors: Array<{ variable: string; error: string }> = [];
    const variablePattern = /\$(\w+)|\$\{([^}]+)\}/g;
    const availableVars = new Set(this.getVariables().map(v => v.substring(1))); // Remove $ prefix
    
    let match;
    while ((match = variablePattern.exec(queryString)) !== null) {
      const variableName = match[1] || match[2];
      
      if (!availableVars.has(variableName)) {
        errors.push({
          variable: `$${variableName}`,
          error: `Variable not found. Available variables: ${Array.from(availableVars).join(', ')}`
        });
      }
    }
    
    return errors;
  }

  // DataSourceWithBackend automatically handles query() and testDatasource() methods
}
